import QRCode from '../../weapp-QR/weapp-qrcode.js'
export default {
	data() {
		return {
			personMsg: null,
			showBtn: true,
			QRcodeImg: null
		}
	},
	onLoad(options) {
		this.personMsg = this.$common.wxMsg
		if (options.uid) {
			this.showBtn = false
		}
		console.log(uni.getStorageSync('user_id'))
		var qrcode = new QRCode('myQrcode', {
			usingIn: this,
			// text: "www.baidu.com",
			text: "https://wx.dtyp.net/index.php/Api/user/userRegister/user_id/" + uni.getStorageSync('user_id'),
			width: uni.upx2px(320),
			height: uni.upx2px(320),
			colorDark: "#000000",
			colorLight: "#ffffff",
			correctLevel: QRCode.CorrectLevel.H,
		});
	},
	methods: {
		saveImage() {
			uni.canvasToTempFilePath({
				x: 0,
				y: 0,
				destWidth: 100,
				destHeight: 100,
				canvasId: 'myQrcode',
				success: (res) => {
					// 在H5平台下，tempFilePath 为 base64
					console.log(res)
					this.QRcodeImg = res.tempFilePath
					uni.saveImageToPhotosAlbum({
						filePath: this.QRcodeImg,
						success: function() {
							console.log('save success');
						}
					});
				}
			})
		},
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			console.log(res.target)
		}
		return {
			title: '扫一扫加入我们',
			path: '/pages/myTeam/code?uid=' + uni.getStorageSync('user').user_id
		}
	},
}
